@use "../base" as b;
@use "../base/mixin" as m;

.lineup {
    background-color: b.$rosa;
    h3, .dia {
        text-align: center;
        color: b.$blanco
    }

    .dia {
        font-size: 3rem;
        text-transform: uppercase;
    }
}

.escenarios-contenedor {
    @include m.contenedor;
    @include m.tablet {
        @include m.grid(2, 5rem)
    }
}

.escenario {
    padding-top: 2rem;
    .nombre-escenario {
        font-size: 3rem;
        font-weight: 900;
        text-transform: uppercase;
        text-align: center;
    }
}

.calendario {
    @include m.resetear-lista;
    li {
        padding: 2rem;
        font-weight: 700;
        span {
            font-weight: 400;
        }
    }

}

.bg-amarillo {
    background-color: b.$amarillo;
    li:nth-child(odd) {
        background-color: lighten(b.$amarillo, 5%);
    }
}

.bg-verde {
    background-color: b.$verde;
    li:nth-child(odd) {
        background-color: lighten(b.$verde, 5%);
    }
}